.card {
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  cursor: pointer;
  height: 170px;
  margin: 15px;
  width: 170px;
}

.card:hover {
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  transform: scale(1.05);
  transition: 0.4s;
}

.card-img-top {
  border-radius: 0;
}

.card-height {
  height: 100% !important;
  width: 100% !important;
}
